a {
	text-decoration: none !important;
}
ul {
	margin: 0 !important;
	padding: 0 !important;
	list-style: none !important;
}
body {
	font-family: "Manrope", sans-serif;
	overflow-x: hidden !important;
	opacity: 0;
	transition: all .3s ease;
	&.loaded{
		opacity: 1;
		transition: all .3s ease;
	}

}
:focus-visible {
	outline: none !important;
}
.section {
	overflow-x: hidden !important;
}
:focus {
	box-shadow: none !important;
	border: none !important;
}
::-moz-selection {
	color: #fff;
	background: #4f46ba;
}

::selection {
	color: #fff;
	background: #4f46ba;
}
// Default styles
.title {
	font-weight: 800;
	font-size: 54px;
	line-height: 1.18;
	color: #000000;
	@media (max-width: 768px) {
		font-size: 36px;
	}
	@media (max-width: 450px){
		font-size: 30px;
	}
}
.subtitle {
	font-weight: 700;
	font-size: 20px;
	line-height: 1.35;
}
.text {
	font-family: "Gilroy SemiBold";
	font-size: 14px;
	line-height: 1.14;
}
.subtitle-big {
	font-weight: 500;
	font-size: 18px;
	line-height: 1.61;
}
.subtitle-md {
	font-family: "Gilroy SemiBold";
	font-size: 22px;
	line-height: 1.18;
	color: #000000;
}
.subtext {
	font-weight: 400;
	font-size: 16px;
	line-height: 1.62;
}
.text-lg {
	font-weight: 400;
	font-size: 12px;
}
